<template>
    <div class="xrr">
        <audio src="http://www.ytmp3.cn/down/50494.mp3" loop="loop" autoplay="autoplay">
            Your browser does not support the audio element.
        </audio>
        <p class="x0">你一定很好奇这是什么吧？</p>
        <p class="x1">请往下看...</p>
        <img src="@/assets/xrr/pointer.gif" alt="">

        <img class="dinner" src="@/assets/xrr/1.jpeg" alt="">
        <p class="x2">我们在一起的日子...</p>
        <img class="inter" src="@/assets/xrr/inter.gif" alt="">
        <p class="who">很怀念....</p>

        <p class="other">
            啦啦啦...
        </p>

        <img class="dinner" src="@/assets/xrr/2.jpeg" alt="">
        <img class="dinner" src="@/assets/xrr/3.jpeg" alt="">
        <img class="dinner" src="@/assets/xrr/4.jpeg" alt="">
        <img class="dinner" src="@/assets/xrr/5.jpeg" alt="">
        <img class="dinner last" src="@/assets/xrr/6.jpeg" alt="">
        <p class="x10">哈哈.....认识你很幸运，OY！！</p>


        <div class="textArea">
            <p class="extra">附:</p>
            <p class="homework">
                希望你的每一天都快乐，要幸福
            </p>
        </div>
        <!--<img class="inter" src="/static/images/xrr/inter.gif" alt="">-->
        <!--<p class="who">是谁是谁你是谁....</p>-->
        <div class = "sure1" @click = "searchAll">显示所有数据</div><br/>

        <div class="remove" @click = "remove">删除所有数据</div><br/><br/>

        <input type="text" class="searchOne" v-model="age"><label class="ageFirst">按年龄查询首条数据</label><span class = "sure2" @click = "sure2">确定</span><br/><br/>
        <input type="text" class="searchAll" v-model="ageAll"><label class="ageFirst">按年龄查询所有数据</label><span class = "sure2" @click = "sure1">确定</span><br/><br/>

        <input type="text" class="addName" v-model="addName">姓名 <input type="text" class="addAge" v-model="addAge">年龄  <span class="sure3" @click="add">添加</span>

        <table v-show = 'isShow' class="modifyTable">
            <tr>
                <th>姓名</th>
                <th>年龄</th>
                <th rowspan="2" @click="confirmModify">修改</th>
            </tr>
            <tr>
                <td>
                    <input type="text" v-model = "modifyName">
                </td>
                <td>
                    <input type="text" v-model = "modifyAge">
                </td>
            </tr>
        </table>

        <table class="table">
             <tr>
                 <th style="width:30%;">姓名</th>
                 <th style="width:30%;">年龄</th>
                 <th style="width:40%;">操作</th>
             </tr>
             <tr v-for="(item,index) in db" :key = index>
                 <td>{{ item.name }}</td>
                 <td>{{ item.age }}</td>
                 <td class="operator">
                     <span @click="modify(item.name,item.age,item._id)">修改</span>
                     <span @click="deleteOne(item._id)">删除</span>
                 </td>
             </tr>
         </table>
         <div class="remind" v-if="db.length===0">暂无数据</div>

    </div>
</template>
<script>
    export default {
        name: "xrr",
        data(){
            return {
                db:[],
                age:'',
                addName:'',
                addAge:'',
                ageAll:'',
                id:'',
                modifyName:'',
                modifyAge:'',
                isShow:false,

            }
        },
        created(){
            // this.$axios.get('/api/lottery/d11x5/recent_list', {
            //     params:{
            //         lottery_id:201,
            //     }
            // }).then(res => {
            //     let data = res.data;
            //     if (data.code === 200) {
            //         console.log(data);
            //     } else {
            //         console.log("接口请求错误");
            //     }
            // }).catch(error => {
            //     console.log(error)
            // });


          //默认加载所有用户信息
             this.searchAll();
        },
        methods:{
            //修改用户信息
            modify(name,age,id){
                this.modifyName = name;
                this.modifyAge = age;
                this.id = id;
                this.isShow = true;
            },
            //确认修改
            confirmModify(){
                //修改某条用户信息
                this.$axios.get('/mongodb/updateOne', {
                    params:{
                        id:this.id,
                        name:this.modifyName,
                        age:this.modifyAge,
                    }
                }).then(res => {
                    if(res.data.n === 1){
                        this.searchAll();
                    }
                }).catch(error => {
                    console.log(error)
                });
                this.isShow = false;
            },
            //查询所有数据
            searchAll(){
                this.$axios.get('/mongodb/find', {
                }).then(res => {
                    this.db = res.data;
                    this.age = '';
                }).catch(error => {
                    console.log(error)
                });
            },
            //查询满足条件的所有用户信息
            sure1(){
                this.$axios.get('/mongodb/findAccess', {
                    params:{
                        age:this.ageAll,
                    }
                }).then(res => {
                    this.db = res.data;
                    this.ageAll = '';
                }).catch(error => {
                    console.log(error)
                });
            },
            //查询首条满足条件的用户信息
            sure2(){
                this.$axios.get('/mongodb/findOne', {
                    params:{
                        age:this.age,
                    }
                }).then(res => {
                    this.db = [];
                    if(res.data){
                        this.db.push(res.data);
                        this.age = '';
                    }

                }).catch(error => {
                    console.log(error)
                });
            },
            //添加某条用户信息
            add(){
                this.$axios.get('/mongodb/insertOne', {
                    params:{
                        name:this.addName,
                        age:this.addAge,
                    }
                }).then(res => {
                    console.log(res);
                    if(res.data.n === 1){
                        this.searchAll();
                        this.addName = '';
                        this.addAge = '';
                    }
                }).catch(error => {
                    console.log(error)
                });
            },
            //删除某条用户信息
            deleteOne(id){
                this.$axios.get('/mongodb/deleteOne', {
                    params:{
                        id:id,
                    }
                }).then(res => {
                    console.log(res);
                    if(res.data.n === 1){
                        this.searchAll();
                    }
                }).catch(error => {
                    console.log(error)
                });
            },
            //删除所有用户信息
            remove(){
                this.$axios.get('/mongodb/remove', {
                }).then(res => {
                    console.log(res);
                    if(res.data.ok === 1){
                        this.searchAll();
                    }
                }).catch(error => {
                    console.log(error)
                });
            }
        }
    }
</script>

<style scoped lang="less">
    @import "../assets/css/vrem.less";
    .xrr{
        background-color: #fff;
        .toRem(padding-bottom,100);
    }
    .dinner{
        width:100%;
        .toRem(margin-bottom,30);
    }
    .last{
        margin-bottom:0;
    }
    .x0{
        .toRem(padding,60);
        .toRem(font-size,100);
        color:pink;
    }
    .x1{
        .toRem(font-size,60);
        text-align: center;
        color:#000000;
    }
    .x2{
        .toRem(font-size,50);
        .toRem(padding-left,40);
    }
    .x10{
        .toRem(font-size,50);
        text-align: center;
        .toRem(line-height,100);
        color:pink;
    }
    .inter{
        width:50%;
        .toRem(margin-top,30);
        margin-left:50%;
        transform:translateX(-50%);
    }
    .who{
        text-align: center;
        .toRem(font-size,50);
        .toRem(margin-top,20);

    }
    .other{
        .toRem(font-size,50);
        .toRem(padding-left,40);
        .toRem(margin-top,80);
        .toRem(margin-bottom,50);
    }
    .textArea{
        width:90%;
        margin:0 auto;
        .toRem(margin-top,100);
        .toRem(margin-bottom,40);
        /*<!--.toRem(margin-bottom,100);-->*/
        .toRem(height,180);
        border:1px solid lightBlue;
        .extra{
            .toRem(font-size,30);
            .toRem(line-height,80);
            .toRem(padding-left,40);
        }
        .homework{
            .toRem(padding-left,40);
            .toRem(font-size,30);
        }
    }
    .table{
        .toRem(font-size,30);
        width:96%;
        margin:0 auto;
        .toRem(margin-top,40);
        .toRem(margin-bottom,40);
        border: 1px solid black;
        border-collapse: collapse;
        td{
            border:1px solid black;
            .toRem(line-height,80);
            text-align:center;
        }
        th{
            .toRem(line-height,80);
        }

    }
    .searchOne{
        .toRem(font-size,30);
        .toRem(width,300);
        .toRem(margin-left,70);
    }
    .searchAll{
        .toRem(font-size,30);
        .toRem(width,300);
        .toRem(margin-left,70);
    }

    .ageFirst{
        .toRem(font-size,30);
    }
    .sure2{
        .toRem(font-size,30);
        display:inline-block;
        .toRem(margin-left,80);
        background-color: lightblue;
        color:white;
    }
    .remind{
        .toRem(font-size,30);
        .toRem(margin-left,70);
    }
    .sure1{
        .toRem(font-size,30);
        .toRem(margin-left,70);
        display:inline-block;
        background-color: lightblue;
        color:white;
        .toRem(margin-bottom,40);
    }
    .remove{
        .toRem(font-size,30);
        .toRem(margin-left,70);
        display:inline-block;
        background-color: lightblue;
        color:white;
        .toRem(margin-bottom,40);
    }
    .addName{
        .toRem(font-size,30);
        .toRem(margin-left,70);
        .toRem(width,140);
    }
    .addAge{
        .toRem(font-size,30);
        .toRem(margin-left,30);
        .toRem(width,100);
    }
    .sure3{
        .toRem(font-size,30);
        .toRem(margin-left,70);
        display:inline-block;
        background-color: lightblue;
        color:white;
    }
    td{
        .toRem(height,100);
        .toRem(line-height,100);
    }
    .operator{
        text-align: center;
        span{
            color:white;
            display:inline-block;
            .toRem(line-height,80);
            .toRem(margin-right,40);
            background-color: skyblue;
            &:nth-child(2){
                margin-right:0;
            }
        }
    }
    .modifyTable{
        border: 1px solid black;
        border-collapse: collapse;
        .toRem(font-size,30);
        width:96%;
        position:fixed;
        top:50%;
        left:50%;
        transform: translate(-50%,-50%);
        background-color: lightgrey;
        td{
            .toRem(height,120);
            .toRem(line-height,120);
        }
    }
</style>


